.carousel-control-prev-icon, .carousel-control-next-icon {
  height: 100px;
  width: 100px;
  /* outline: yellow; */
  background-color: rgba(204, 18, 18, 0.9);
  background-size: 100%, 100%;
  border-radius: 50%;
  border: 1px solid rgb(6, 248, 67);
  /* color: #ea5a0b; */
}

/* center the photos horizontally */
.carousel-inner > .item > img{
    margin:auto;
}
/* color of controls */
.glyphicon.glyphicon-chevron-right, .glyphicon.glyphicon-chevron-left {
    color: #ea5a0b;
