
.selector {
  margin: 0;
  padding: 5px 0px;
  list-style: none;
  float: right;
}
.selector > li {
  display: inline-block;
  padding: 0 0.5rem;
}
.selector button {
  background-color: white;
  box-shadow: none;
  border: 1px solid #01344a;
  padding: 5px;
  border-radius: 4px;
  /* min-width: 100px; */
  transition: all 0.3s ease-out;
}
.selector button.active {
  color: #212d30;
  background-color: #20232a; 
}
